import useStyles from 'isomorphic-style-loader/useStyles';
// import cx from 'classnames';
import React from 'react';
import PropTypes from 'prop-types';
import plural from 'plural-ru';
import Link from '../../components/Link';
import s from './Experiences.css';

export default function Experiences({ articles }) {
  useStyles(s);
  return (
    <div className={s.root}>
      <div className={s.container}>
        <h1>Экскурсии</h1>
        <p>
          300+ экскурсий в Калининграде с опытными гидами, цены на прогулки от
          450 ₽. Необычные экскурсии от местных жителей. Смотрите расписание на
          май-июнь 2021 года, выбирайте маршрут по Калининграду и бронируйте
          билеты онлайн.
        </p>
        <div className={s.articleContainer}>
          {articles.map(item => (
            <article className={s.newsItem} key={item.title}>
              <Link to={item.to}>
                <div>
                  <div className={s.newsLabels}>
                    {item.itemsCount > 0 && (
                      <div className={s.newsLabel}>
                        <span role="img" aria-label="rating">
                          ⭐ {item.itemsCount}{' '}
                          {plural(
                            item.itemsCount,
                            'экскурсия',
                            'экскурсии',
                            'экскурсий',
                          )}
                        </span>
                      </div>
                    )}
                  </div>
                  <div style={{ height: 220 }}>
                    {item.media && <img src={item.media} alt="" />}
                  </div>
                  <h3>{item.title}</h3>
                </div>
              </Link>
            </article>
          ))}
        </div>
      </div>
    </div>
  );
}

Experiences.propTypes = {
  articles: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
    }),
  ).isRequired,
};
